import React, { useRef } from "react";
import { SignatureCanvasWrapper, SignatureArea } from "../../components/SignatureCanvasWrapper";
import Container from "../../components/Container";
import { Title, SectionTitle } from "../../components/Title";
import { Button, ButtonGroup } from "../../components/Button";
import { InputData, InputRow } from "../../components/Input";
import { Paragraph } from "../../components/Paragraph";

const FifthPage = ({ prevPage }) => {
  const signatureRef1 = useRef(null);
  const signatureRef2 = useRef(null);

  const clearSignature1 = () => {
    signatureRef1.current.clear();
  };

  const clearSignature2 = () => {
    signatureRef2.current.clear();
  };

  return (
    <Container>
      <Title>Tratamento Sugerido</Title>

      <InputRow>
        <InputData type="text" placeholder="Tratamento" />
        <InputData type="text" placeholder="N de Sesses" />
      </InputRow>

      <InputRow>
        <InputData type="text" placeholder="Valor por Sesso (R$)" />
        <InputData type="text" placeholder="Valor Total (R$)" />
      </InputRow>

      <InputRow>
        <InputData type="text" placeholder="Pr-agendamento" />
        <InputData type="text" placeholder="Horrio" />
      </InputRow>

      <SectionTitle>Contrato de Servios Estticos</SectionTitle>

      <Paragraph>
        1) Neste ato, a parte acima identificada contrata o tratamento sugerido
        pelo servio da Esteticista e Cosmetloga.
      </Paragraph>
      <Paragraph>
        2) O tratamento sugerido ser condicionado ao prvio pagamento e poder
        ser realizado diretamente pela responsvel.
      </Paragraph>
      <Paragraph>
        3) O tratamento sugerido no impede a realizao de outros
        complementares ou a ele relacionados, por mtuo consentimento das
        partes, dispensando a formalizao de um novo contrato.
      </Paragraph>
      <Paragraph>
        4) A data/horrio de cada sesso poder ser reagendada at o limite de
        duas vezes mediante solicitao com antecedncia mnima de 24 horas.
      </Paragraph>
      <Paragraph>
        5) Caso o cliente atrase ou falte, o valor pago no ser restitudo, a
        ttulo de multa contratual.
      </Paragraph>
      <Paragraph>
        6) O cliente concede o direito de registrar imagens, vdeos ou dados
        relacionados ao tratamento.
      </Paragraph>
      <Paragraph>
        7) O cliente declara estar ciente que todo tratamento possui riscos.
      </Paragraph>
      <Paragraph>
        8) O cliente declara que no possui doenas ou quadros clnicos que
        prejudiquem o tratamento.
      </Paragraph>
      <Paragraph>
        9) Todas as sesses sero realizadas em conformidade com as regras do
        instrumento.
      </Paragraph>
      <Paragraph>
        10) Em cumprimento  Lei 13.709/2018, o cliente autoriza o uso de dados
        para os fins previstos no contrato.
      </Paragraph>
      <Paragraph>
        11) As partes elegem o foro da comarca de Erechim/RS para dirimir
        eventuais litgios.
      </Paragraph>

      <SignatureArea>
        <SignatureCanvasWrapper
                   refCanvas={signatureRef1}
                   onClear={clearSignature1}
                   label="Assinatura da CONTRATANTE"                  
                 />
        <SignatureCanvasWrapper
                   refCanvas={signatureRef2}
                   onClear={clearSignature2}
                   label="Assinatura da CONTRATADA"
                 />
      </SignatureArea>
      <ButtonGroup>
        <Button onClick={prevPage}>Voltar</Button>
      </ButtonGroup>
    </Container>
  );
};

export default FifthPage;
